import React from "react";
import styles from "./Beneficios.module.css";
import bike from "../../Assets/bike.svg";

function Beneficios() {
  return (
    <div className={styles.beneficios}>
      <div className={styles.bike}>
        <img src={bike} />
      </div>
      <section className={styles.box}>
        <div className={styles.images}></div>
        <div className={styles.content}>
          <h1>TOMOGRAFIA</h1>
          <p>
            Tomografia Computadorizada Multislice, uma inovação em Luís Eduardo
            Magalhães, é um dos destaques do Hospital Silvestre.
          </p>
          <p>
            O equipamento conta com reconstrução de 16 canais, garantindo maior
            qualidade e também agilidade no resultado dos exames, já que o laudo
            é entregue em até{" "}
            <span style={{ color: "#9C7119" }}> 2 horas.</span>
          </p>
        </div>
      </section>
      <section className={styles.box2}>
        <div className={styles.content}>
          <h1>UTI</h1>
          <p>
            A inauguração da primeira UTI de LEM se consolidou, disponibilizando
            leitos intensivos concomitante ao isolamento, equipamentos novos e
            de alta tecnologia, incluindo uma equipe vasta e capacitada para
            atendimentos de alto risco.
          </p>
        </div>
        <div className={styles.images2}></div>
      </section>
      <section className={styles.box3}>
        <div className={styles.images3}></div>
        <div className={styles.content}>
          <h1>MATERNIDADE</h1>
          <p>
            O Hospital Silvestre aperfeiçoou sua estrutura e ampliou sua
            maternidade, que agora funciona 24 horas.{" "}
          </p>
          <p>
            {" "}
            Apartamentos exclusivos e consultoria em amamentação também estão
            disponíveis. É mais conforto e comodidade para mãe e filho!
          </p>
        </div>
      </section>
    </div>
  );
}

export default Beneficios;
