import React from "react";

import styles from "./Consultas.module.css";
import divisor from "../../Assets/divisor.svg";
import medicos from "../../Assets/medicos.svg";
import ortopedia from "../../Assets/ortopedia.svg";

import cardiologia from "../../Assets/cardiologia.svg";
import cirurgia from "../../Assets/cirurgia.svg";
import ginecologia from "../../Assets/ginecologia.svg";
import pediatria from "../../Assets/pediatria.svg";
import psiquiatria from "../../Assets/psiquiatria.svg";

import agendar from "../../Assets/agendar.svg";
import list from "../../Assets/list.svg";
import { NavLink } from "react-router-dom";

import Aos from "aos";
import "aos/dist/aos.css";

function Consultas() {
  React.useEffect(() => {
    Aos.init({ duration: 2000 });
  }, []);
  return (
    <>
      <img src={divisor} className={styles.divisor} />{" "}
      <section className={styles.consulta}>
        <div data-aos="fade-right" className={styles.imagem}>
          <img src={medicos} />
        </div>
        <div data-aos="fade-up" className={styles.consultaContent}>
          <span className={styles.titlemargin}>
            <h1 className="title">Especialidades</h1>
          </span>
          <div className={styles.box}>
            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={ortopedia} />
                </div>
                <span> ORTOPEDIA </span>
              </div>
            </NavLink>
            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={pediatria} />
                </div>
                <span> PEDIATRIA </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={ginecologia} />
                </div>
                <span> GINECOLOGIA </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={psiquiatria} />
                </div>
                <span> PSIQUIATRIA </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={cardiologia} />
                </div>
                <span> CARDIOLOGIA </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={cirurgia} />
                </div>
                <span> CIRURGIA GERAL </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button3}>
                <div className={styles.logos}>
                  <img src={list} />
                </div>
                <span> VER TODAS </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button2}>
                <div className={styles.logos}>
                  <img src={agendar} />
                </div>
                <span> AGENDAR </span>
              </div>
            </NavLink>
          </div>
        </div>
      </section>
    </>
  );
}

export default Consultas;
