import React, { useState } from "react";
import { SliderData } from "./SliderData";
import "./Slider.css";
import { NavLink } from "react-router-dom";
import arrow from "../Assets/arrow.svg";

const Slider = ({ slides }) => {
  const [current, setCurrent] = useState(0);
  const length = slides.length;

  const nextSlide = () => {
    setCurrent(current === length - 1 ? 0 : current + 1);
  };

  const prevSlide = () => {
    setCurrent(current === 0 ? length - 1 : current - 1);
  };

  if (!Array.isArray(slides) || slides.length <= 0) {
    return null;
  }

  return (
    <>
      <div className="container-slide-mobile">
        <div className="image"></div>
        <div className="content-mobile">
          <h1 className="title-slide">Resultado Exames</h1>
          <p className="text-slide">Visualize os resultados do exame</p>
          <NavLink className="button-slide" to="/resultados">
            Ver Resultado
          </NavLink>
        </div>
      </div>

      <section className="slider">
        {SliderData.map((slide, index) => {
          return (
            <div
              className={index === current ? "slide active" : "slide"}
              key={index}
            >
              <div className="container-web">
                {index === current && (
                  <div className="container-slide">
                    <section className="texto">
                      <h1 className="title-slide">{slide.h1}</h1>
                      <p className="text-slide">
                        {slide.text ? slide.text : null}
                      </p>
                      <NavLink className="button-slide" to={slide.path}>
                        {slide.button}
                      </NavLink>
                    </section>
                    <div className="imagem">
                      <img src={slide.image} />
                    </div>
                  </div>
                )}
              </div>
            </div>
          );
        })}
        <div className="arrow">
          <div className="right-arrow" onClick={nextSlide}>
            <img src={arrow} />
          </div>
          <div className="left-arrow" onClick={prevSlide}>
            <img src={arrow} />
          </div>
        </div>
      </section>
    </>
  );
};

export default Slider;
