import React from "react";
import { NavLink } from "react-router-dom";
import noticia1 from "../../Assets/noticia1.jpg";
import noticia2 from "../../Assets/noticia2.jpg";
import noticia3 from "../../Assets/noticia3.jpg";
import noticia4 from "../../Assets/noticia5.jpg";

import styles from "./Noticias.module.css";

function Noticias() {
  return (
    <>
      <span className={styles.h1}>
        <h1 className={styles.title}>Notícias</h1>
      </span>
      <section className={styles.noticias}>
        <div className={styles.containerImage}>
          <div
            style={{ backgroundImage: `url(${noticia1})` }}
            className={styles.image}
          ></div>
          <div
            style={{ backgroundImage: `url(${noticia2})` }}
            className={styles.image}
          ></div>
          <div
            style={{ backgroundImage: `url(${noticia3})` }}
            className={styles.image}
          ></div>
          <div
            style={{ backgroundImage: `url(${noticia4})` }}
            className={styles.image}
          >
            <img />
          </div>
        </div>
      </section>
      <NavLink
        className={styles.mais}
        style={{ fontSize: "1.5rem", borderBottom: "1px solid" }}
        to="/noticias"
      >
        Ver Mais
      </NavLink>
    </>
  );
}

export default Noticias;
