import React from "react";

import styles from "./Perguntas.module.css";
import { NavLink } from "react-router-dom";
function Perguntas() {
  const [modal, setModal] = React.useState(false);
  const [modal2, setModal2] = React.useState(false);

  const [modal3, setModal3] = React.useState(false);

  return (
    <div className={styles.perguntas}>
      <h1 className="title">Perguntas Frequentes</h1>
      <ul className={`${styles.pergunta} teste `}>
        <li
          className={modal && styles.active}
          onClick={() => {
            setModal(!modal);
          }}
        >
          Como faço para marcar uma consulta no Silvestre?
        </li>
        <p className={modal ? styles.active : styles.none}>
          As consultas com especialistas devem ser agendadas por
          <NavLink to="/contato"> telefone / whatsapp</NavLink>. Veja também as
          <NavLink to="/especialidades"> especialidades disponíveis.</NavLink>
        </p>

        <li
          className={modal2 && styles.active}
          onClick={() => {
            setModal2(!modal2);
          }}
        >
          Como faço para acessar os resultados de exames?
        </li>
        <p className={modal2 ? styles.active : styles.none}>
          Os resultados de exames podem ser acessador na sessão de resultados.
          Clique em <NavLink to="/resultados"> resultados exames </NavLink>e vá
          direto para a sessão.
        </p>

        <li
          className={modal3 && styles.active}
          onClick={() => {
            setModal3(!modal3);
          }}
        >
          Como faço para marcar exames no Hospital Silvestre?
        </li>
        <p className={modal3 ? styles.active : styles.none}>
          Você pode utilizar um dos nossos canais de atendimento por telefone -
          <NavLink to="/contato"> (77) 99984-6579</NavLink> ou entre em contato
          através dos números (77) 3628-7499 e (77) 3628.3975
        </p>
      </ul>
    </div>
  );
}

export default Perguntas;
