import React from "react";
import styles from "./Footer.module.css";

import insta from "../../Assets/insta.svg";
import wpp from "../../Assets/wpp.svg";
import face from "../../Assets/face.svg";
import { NavLink } from "react-router-dom";

const Footer = () => {
  var date = new Date().getFullYear();

  return (
    <footer className={styles.footer}>
      <nav>
        <section className={styles.central}>
          <h2 className={styles.title}>Central de Ajuda</h2>
          <ul>
            <li>Fale Conosco</li>
            <li>Ouvidoria</li>
            <li>Materiais Informativos</li>
          </ul>
        </section>
        <section className={styles.central}>
          <h2>Contatos</h2>
          <ul>
            <li>
              <p class="font-contato">(77) 3628-7499</p>
            </li>
            <li>
              <p class="font-contato">
                <a href="https://bit.ly/3GzLJmg">(77) 99984-6579</a>
              </p>
            </li>
          </ul>
        </section>
        <section className={styles.central}>
          <h2>Central de Atendimentos</h2>
          <ul>
            <li>
              <a href="">Quem somos</a>
            </li>
            <li>
              <a href="">Emergência </a>
            </li>
            <li>
              <a href="">Especialidades</a>
            </li>
            <li>
              <a href="">Doenças</a>
            </li>
          </ul>
        </section>
        <section className={styles.redes}>
          <ul>
            <a href="https://www.facebook.com/silvestrehospital/">
              <li>
                <img src={face} />
              </li>
            </a>
            <a href="https://bit.ly/3GzLJmg">
              <li>
                <img src={wpp} />
              </li>
            </a>
            <a href="https://www.instagram.com/hospital.silvestre/">
              <li>
                <img src={insta} />
              </li>
            </a>
          </ul>
        </section>
        <div class={styles.copyright}>
          <p>
            Copyright © {date} Hospital Silvestre.
            <a href="">Política de Privacidade</a>
            <a href=""> Trabalhe Conosco</a> <a href=""> Fornecedores</a>
            <a class="imprensa" href="">
              Imprensa
            </a>
          </p>
        </div>
      </nav>
    </footer>
  );
};

export default Footer;
