import React from "react";

import styles from "./Exames.module.css";
import divisor from "../../Assets/divisor.svg";
import medicos from "../../Assets/medicos2.svg";

import agendar from "../../Assets/agendar.svg";
import raiox from "../../Assets/raiox.svg";
import ultrasson from "../../Assets/ultrasson.svg";
import endoscopia from "../../Assets/endoscopia.svg";
import analise from "../../Assets/analise.svg";
import list from "../../Assets/list.svg";
import { NavLink } from "react-router-dom";

import Aos from "aos";
import "aos/dist/aos.css";
function Exames() {
  React.useEffect(() => {
    Aos.init({ duration: 2000 });
  }, []);

  return (
    <>
      <img src={divisor} className={styles.divisor} />{" "}
      <section className={styles.consulta}>
        <div data-aos="fade-up" className={styles.consultaContent}>
          <span className={styles.titlemargin}>
            <h1 className="title">Exames </h1>
          </span>
          <div className={styles.box}>
            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={analise} />
                </div>
                <span> Análises Clínicas </span>
              </div>
            </NavLink>
            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={ultrasson} />
                </div>
                <span> Ultrassonografia </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={raiox} />
                </div>
                <span> Raio X </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button}>
                <div className={styles.logos}>
                  <img src={endoscopia} />
                </div>
                <span> Endoscopia </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button3}>
                <div className={styles.logos}>
                  <img src={list} />
                </div>
                <span> VER TODAS </span>
              </div>
            </NavLink>

            <NavLink to="/">
              <div className={styles.button2}>
                <div className={styles.logos}>
                  <img src={agendar} />
                </div>
                <span> AGENDAR </span>
              </div>
            </NavLink>
          </div>
        </div>
        <div data-aos="fade-left" className={styles.imagem}>
          <img src={medicos} />
        </div>
      </section>
    </>
  );
}

export default Exames;
