import React from "react";
import styles from "./Header.module.css";
import logo from "../../Assets/logo.svg";
import { NavLink, useLocation } from "react-router-dom";

function Header() {
  const [modal, setModal] = React.useState(false);
  const [modal2, setModal2] = React.useState(false);
  const [modal3, setModal3] = React.useState(false);
  const [modal4, setModal4] = React.useState(false);

  const [mobile, setMobile] = React.useState(false);

  function sobreClick() {
    setModal(!modal);
    setModal2(false);
    setModal3(false);
    setModal4(false);
  }
  function pacientesClick() {
    setModal(false);
    setModal2(!modal2);
    setModal3(false);
    setModal4(false);
  }
  function empresasClick() {
    setModal(false);
    setModal2(false);
    setModal3(!modal3);
    setModal4(false);
  }
  function serviçosClick() {
    setModal(false);
    setModal2(false);
    setModal3(false);
    setModal4(!modal4);
  }
  const { pathname } = useLocation();

  React.useEffect(() => {
    setModal(false);
    setModal2(false);
    setModal3(false);
    setModal4(false);
  }, [pathname]);

  React.useEffect(() => {
    setMobile(false);
  }, [pathname]);

  return (
    <div>
      <header className={styles.header}>
        <nav>
          <NavLink to="/">
            <img className={styles.logo} src={logo} />
          </NavLink>
          <ul>
            <li className={modal && styles.active} onClick={sobreClick}>
              Quem somos?
            </li>
            <li className={modal2 && styles.active} onClick={pacientesClick}>
              Pacientes
            </li>
            <li className={modal3 && styles.active} onClick={empresasClick}>
              Empresas
            </li>
            <li className={modal4 && styles.active} onClick={serviçosClick}>
              Serviços
            </li>
          </ul>
          <button
            aria-label="Menu"
            onClick={() => {
              setMobile(!mobile);
            }}
            className={`${styles.mobileButton} ${
              mobile && styles.mobileButtonActive
            }`}
          ></button>
        </nav>
      </header>
      <div className={styles.test}>
        <section className={modal ? styles.modal : styles.none}>
          <h1 className={styles.title}>QUEM SOMOS</h1>
          <ul className={styles.lis}>
            <NavLink to="/sobre">
              <li>Sobre o Silvestre</li>
            </NavLink>
            <NavLink to="/atendimento">
              <li>Fale conosco</li>
            </NavLink>
            <NavLink to="/qualidade">
              <li>Qualidade e Segurança</li>
            </NavLink>
          </ul>
          <ul className={styles.lis}>
            {" "}
            <NavLink to="/pronto-atendimento">
              <li>Pronto Atendimento</li>
            </NavLink>
            <NavLink to="/trabalhe-conosco">
              <li>Trabalhe conosco</li>
            </NavLink>
          </ul>
        </section>
        <section className={modal2 ? styles.modal : styles.none}>
          <h1 className={styles.title}>PACIENTES</h1>
          <ul className={styles.lis}>
            <NavLink to="/planos-convenios">
              <li>Planos de saúde </li>
            </NavLink>
            <NavLink to="/pronto-atendimento">
              <li>Pronto Atendimento</li>
            </NavLink>
            <NavLink to="/contato">
              <li>Agendamento</li>
            </NavLink>
          </ul>
          <ul className={styles.lis}>
            {" "}
            <NavLink to="/resultados">
              <li>Resultados exames</li>
            </NavLink>
            <NavLink to="/noticias">
              <li>Notícias</li>
            </NavLink>
          </ul>
        </section>
        <section className={modal3 ? styles.modal : styles.none}>
          <h1 className={styles.title}>EMPRESAS</h1>
          <ul className={styles.lis}>
            <NavLink to="/saude">
              <li>Saúde Populacionais</li>
            </NavLink>
            <NavLink to="/consultoria">
              <li>Consultoria</li>
            </NavLink>
            <NavLink to="/check-up-empresas">
              <li>Check-up para empresas</li>
            </NavLink>
          </ul>
        </section>
        <section className={modal4 ? styles.modal : styles.none}>
          <h1 className={styles.title}>SERVIÇOS</h1>
          <ul className={styles.lis}>
            <NavLink to="/resultados">
              <li>Resultados exames</li>
            </NavLink>
            <NavLink to="/tecnologia">
              <li>Tecnologia médicas</li>
            </NavLink>
            <NavLink to="/contato">
              <li>Agendar</li>
            </NavLink>
          </ul>
          <ul className={styles.lis}>
            {" "}
            <NavLink to="/especialidades">
              <li>Especialidades</li>
            </NavLink>
            <NavLink to="/exames">
              <li>Exames</li>
            </NavLink>
            <NavLink to="/noticias">
              <li>Notícia</li>
            </NavLink>
          </ul>
        </section>
        <div className={mobile ? styles.mobile : styles.none}>
          <section>
            <h1 className={styles.title}>Quem somos</h1>
            <ul>
              <NavLink to="/sobre">
                <li>Sobre o Silvestre</li>
              </NavLink>
              <NavLink to="/atendimento">
                <li>Fale conosco</li>
              </NavLink>
              <NavLink to="/qualidade">
                <li>Qualidade e Segurança</li>
              </NavLink>
              <NavLink to="/pronto-atendimento">
                <li>pronto atendimento</li>
              </NavLink>
            </ul>
          </section>
          <section>
            <h1 className={styles.title}>Pacientes</h1>
            <ul>
              <NavLink to="planos-convenios">
                <li>Planos de saúde</li>
              </NavLink>
              <NavLink to="contato">
                <li>Agendamentos</li>
              </NavLink>
              <NavLink to="resultados">
                <li>Resultados exames</li>
              </NavLink>
              <NavLink to="noticias">
                <li>Notícias</li>
              </NavLink>
            </ul>
          </section>
          <section>
            <h1 className={styles.title}>Empresas</h1>
            <ul>
              <NavLink to="saude">
                <li>Saúde populacionais</li>
              </NavLink>
              <NavLink to="consultoria">
                <li>Consultoria</li>
              </NavLink>
              <NavLink to="check-up-empresas">
                <li>Check-up para empresas</li>
              </NavLink>
            </ul>
          </section>
          <section>
            <h1 className={styles.title}>Serviços</h1>
            <ul>
              <NavLink to="resultados">
                <li>Resultados exames</li>
              </NavLink>
              <NavLink to="tecnologia">
                <li>Tecnologia médicas</li>
              </NavLink>
              <NavLink to="especialidades">
                <li>Especialidades</li>
              </NavLink>
              <NavLink to="exames">
                <li> Exames</li>
              </NavLink>
            </ul>
          </section>
        </div>
      </div>
    </div>
  );
}

export default Header;
